import React, { Component } from 'react';
import {Container, Row} from 'react-bootstrap';
import Header from './components/Header';
import Home from './components/Home';
import Resume from './components/Resume';
import Portfolio from './components/Portfolio';
import {HashRouter as Router, Switch, Route} from 'react-router-dom';
import config from 'react-reveal/globals';
import Footer from './components/footer';
import './App.css';
 
config({ ssrFadeout: true });

class App extends Component {
   state = {
    // On if dark
    checked: localStorage.getItem("theme") === "dark" ? true : false,
    theme: localStorage.getItem("theme")
  };

  componentDidMount() {
    // Update the data-theme attribute of our html tag
    document
      .getElementsByTagName("HTML")[0]
      .setAttribute("data-theme", localStorage.getItem("theme"));
  }

  // Class method allowing us to toggle the theme change
  toggleThemeChange = () => {
    const { checked } = this.state;
    // If theme is light then change to dark
    if (checked === false) {
      // Update localstorage
      localStorage.setItem("theme", "dark");
      document
        .getElementsByTagName("HTML")[0]
        .setAttribute("data-theme", localStorage.getItem("theme"));
      // Update our state
      this.setState({
        // Ensure our switch is on if we change to dark theme
        checked: true
      });
    } else {
      // Update localstorage
      localStorage.setItem("theme", "light");
      // Update the data-theme attribute of our html tag
      document
        .getElementsByTagName("HTML")[0]
        .setAttribute("data-theme", localStorage.getItem("theme"));
      // Update our state
      this.setState({
        // Ensure our switch is off if we change to light theme
        checked: false
      });
    }
  };

  render() {
    return (
      <Router>
      <Container style={{alignItems: 'center', position:'relative'}}>
        <Switch>
        <Route path={process.env.PUBLIC_URL + '/'} exact render={(props) => (
            <Home {...props} checked={this.state.checked} />
            )}
        />
        <Route path={process.env.PUBLIC_URL + '/about'} component={Resume}/>
        <Route path={process.env.PUBLIC_URL + '/portfolio'} component={Portfolio}/>
        </Switch>
        <Footer/>
        <label class="switch">
            <input
              type="checkbox"
              defaultChecked={this.state.checked}
              onChange={() => this.toggleThemeChange()}
            />
            <div class="slider round">
            <span class="on">ðŸ˜Ž</span><span class="off">ðŸŒ›</span>
            </div>
          </label>
        <Header/>
        
      </Container>
      </Router>
    );
  }
}

export default App;